.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

html {
  box-sizing: border-box;
}

body {
  font-size: 62.5%;
}

#root {
  margin: 0 auto;
}

#canvas-container {
  width: 400px;
  height: 350px;
}

#canvas {
  width: 380px;
  height: 330px;
  position: relative;
  border: 10px solid #35a740;
  color: #35a740;
  font-family: "游明朝", YuMincho, "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "HG明朝E", "ＭＳ Ｐ明朝", "ＭＳ 明朝", 'Noto Serif JP', serif;
}

.words {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);

  top: 77.25px;
  max-height: 249px;

  font-size: 12px;
  letter-spacing: 1.8px;
  line-height: 23px;

  -webkit-writing-mode: vertical-rl;
  -ms-writing-mode: tb-rl;
  writing-mode: vertical-rl;
}

.user-info {
  position: absolute;
  bottom: 13px;
  left: 12px;
  -webkit-writing-mode: vertical-rl;
  -ms-writing-mode: tb-rl;
  writing-mode: vertical-rl;
  font-size: 1em;
  letter-spacing: .7px;
}

.text-combine {
  text-combine-upright: all;
  letter-spacing: 0;
}

.non-vertical {
  -webkit-writing-mode: horizontal-tb;
  writing-mode: horizontal-tb;
  line-height: 1;
}

.heading {
  text-align: center;
  font-family: "游明朝", YuMincho, "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "HG明朝E", "ＭＳ Ｐ明朝", "ＭＳ 明朝", 'Noto Serif JP', serif;
}

.title {
  font-size: 1.5em;
  letter-spacing: 2px;
  margin-right: 10px;
  font-family: "游明朝", YuMincho, "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "HG明朝E", "ＭＳ Ｐ明朝", "ＭＳ 明朝", 'Noto Serif JP', serif;
  -webkit-writing-mode: vertical-rl;
  -ms-writing-mode: tb-rl;
  writing-mode: vertical-rl;
}

#container {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  justify-content: center;
  padding: 20px;
  margin: 0 auto;
  width: 433px;
  color: #35a740;
}

.date {
  line-height: 3.6;
  font-family: "游明朝", YuMincho, "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "HG明朝E", "ＭＳ Ｐ明朝", "ＭＳ 明朝", 'Noto Serif JP', serif;
}

.input-field {
  width: 60%;
  margin: 0 auto;
  padding-bottom: 30px;
  border-bottom: solid 1px lightgray;
}

textarea {
  font: 15px/20px sans-serif;
  box-sizing: border-box;
  width: 100%;
  padding: 0.3em;
  transition: 0.3s;
  letter-spacing: 1px;
  color: #555555;
  border: 1px solid #1b2538;
  border-radius: 4px;
  margin-bottom: 15px;
}

textarea:focus {
  border: 1px solid #da3c41;
  outline: none;
  box-shadow: 0 0 5px 1px rgba(218, 60, 65, .5);
}

input[type='text'] {
  font: 15px/20px sans-serif;
  box-sizing: border-box;
  width: 100%;
  padding: 0.3em;
  transition: 0.3s;
  letter-spacing: 1px;
  color: #555555;
  border: 1px solid #1b2538;
  border-radius: 4px;
  margin-bottom: 15px;
}

input[type='text']:focus {
  border: 1px solid #da3c41;
  outline: none;
  box-shadow: 0 0 5px 1px rgba(218, 60, 65, .5);
}

select {
  box-sizing: border-box;
  width: 100%;
  padding: 0.3em;
  transition: 0.3s;
  letter-spacing: 1px;
  color: #555555;
  border: 1px solid #1b2538;
  border-radius: 4px;
  margin-bottom: 15px;
}

select:focus {
  border: 1px solid #da3c41;
  outline: none;
  box-shadow: 0 0 5px 1px rgba(218, 60, 65, .5);
}

input[type='submit'] {
  display: inline-block;
  border-radius: 5px;
  background-color: green;
  padding: 10px;
  text-align: center;
  color: white;
  width: 80px;
}

.rotate {
  display: inline-block;
  transform: translateX(1px) rotate(90deg);
}